export interface VaultBaseTransactionBody {
  block: Block
  transactionHash: string
  vaultData: VaultContractData
  transactionInput?: Bytes
  amount?: iBigInt
  price?: iBigInt
  tokenAmount?: iBigInt
  walletAddress?: string
  tokenAddress?: string
  tokenData?: ERC20Token
}

export function sVaultBaseTransactionBody() {
  return S.object()
    .additionalProperties(false)
    .prop('block', sBlock())
    .description('Block')
    .required()
    .prop('transactionHash', S.string())
    .description('Transaction hash')
    .required()
    .prop('vaultData', sVaultContractData())
    .description(`VaultBlock data`)
    .required()
    .prop('transactionInput', S.string())
    .description('Transaction input')
    .prop('amount', sBigInt())
    .description(`Amount`)
    .prop('price', sBigInt())
    .description(`Price`)
    .prop('tokenAmount', sBigInt())
    .description(`tokenAmount`)
    .prop('walletAddress', sBCAddress())
    .description('Wallet address')
    .prop('tokenAddress', sBCAddress())
    .description('Token address')
    .prop('tokenData', sERC20Token())
    .description('ERC0 token data')
}

export function sVaultTransferBody() {
  return S.object()
    .additionalProperties(false)
    .prop('fromAddress', sBCAddress())
    .description('Wallet from address')
    .prop('toAddress', sBCAddress())
    .description('Wallet to address')
    .extend(sVaultBaseTransactionBody())
}

