export interface VaultsSearchQuery
  extends PageSearchQuery<'name', VaultFields> {
  name?: string
  cdo?: string
  'name:ct'?: string
  address?: string
  manager?: string
  symbol?: string | string[]
  status?: VaultStatus
  chainId?: string | string[]
  visibility?: VaultVisibility | VaultVisibility[]
  'visibility:ne'?: VaultVisibility | VaultVisibility[]
  contractType?: VaultContractType | VaultContractType[]
  'cdoEpoch.borrower.address'?: string | string[]
  'cdoEpoch.borrower.operatorId'?: string | string[]
  'cdoEpoch.manager.address'?: string | string[]
  'cdoEpoch.manager.operatorId'?: string | string[]
}

export function sVaultsSearchQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('name', S.string())
    .description('Name of the vault that must match.')
    .prop('name:ct', S.string())
    .description('Name of the vault that must be contained.')
    .prop('address', sBCAddress())
    .description('Address of the vault that must match.')
    .prop('status', sVaultStatus())
    .description('Status of the vault that must match.')
    .prop(
      'visibility',
      S.array().minItems(1).maxItems(3).items(sVaultVisibility())
    )
    .description('Visibility of the vault that must match.')
    .prop(
      'visibility:ne',
      S.array().minItems(1).maxItems(3).items(sVaultVisibility())
    )
    .description('Visibility of the vault that must be different.')
    .prop(
      'cdoEpoch.borrower.address',
      S.array().minItems(1).maxItems(200).items(sBCAddress())
    )
    .description(`Borrower's address of the vault that must match.`)
    .prop(
      'cdoEpoch.borrower.operatorId',
      S.array().minItems(1).maxItems(200).items(S.string())
    )
    .description(`Borrower's operator ID of the vault that must match.`)
    .prop(
      'cdoEpoch.manager.address',
      S.array().minItems(1).maxItems(200).items(sBCAddress())
    )
    .description(`Manager's address of the vault that must match.`)
    .prop(
      'cdoEpoch.manager.operatorId',
      S.array().minItems(1).maxItems(200).items(S.string())
    )
    .description(`Manager's operator ID of the vault that must match.`)
    .prop('cdo', S.string())
    .description('CDO of the vault that must match.')
    .prop('symbol', S.array().minItems(1).maxItems(200).items(S.string()))
    .description('Symbol of the vault that must match.')
    .prop('chainId', S.array().minItems(1).maxItems(200).items(sStringId()))
    .description('ChainID of the vault that must match.')
    .prop(
      'contractType',
      S.array().minItems(1).maxItems(200).items(sVaultContractType())
    )
    .description('Contract type of the vault that must match.')
    .extend(sPageSearchQuery(VAULT_FIELDS, VAULT_SORT_FIELDS))
}

